<template>
    <section class="container" :Loading="allLoading">
      <el-tabs type="border-card">
        <el-tab-pane label="">

         <el-row style="border-radius: 4px">
            <el-col :span="6">
                
            </el-col>
           <el-col :span="18">
             

           </el-col>
         </el-row>
          <el-row>
            <el-col :span="6">
              {{dormitoryInfo.rent}}
            </el-col>
            <el-col :span="18">
              <el-button type="primary" @click="zhifubao('',dormitoryInfo.rent)"></el-button>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              {{studentInfo.c.m.price}}
            </el-col>
            <el-col :span="18">
              <el-button type="primary" @click="zhifubao('',studentInfo.c.m.price)"></el-button>
            </el-col>
          </el-row>


<!--           <span> {{{alipaydata}}}</span>-->

          <div v-html="alipaydata"></div>




        </el-tab-pane>
        <el-tab-pane label="">

          <el-row>
            <el-col :span="6" style=" border-radius: 4px">
              
              
              
              
              
            </el-col>


            <el-col :span="12">
              <div class="grid-content bg-purple">
                <el-upload
                  class="upload-demo"
                  ref="upload"
                  :action="actionUrl"
                  :auto-upload="false"
                  :limit="10"
                  multiple
                  accept="application/pdf"
                  :before-upload="beforeUpload"
                  :before-remove="beforeRemove"
                  :on-remove="handleRemove"
                  :on-change="fileChange"
                  :file-list="fileList"
                  :drag="trueFlag">
                  <el-button size="small" type="primary"></el-button>
                  <!--<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>-->
                  <div slot="tip" class="el-upload__tip">pdf2M</div>
                </el-upload>
              </div>
              <el-button type="primary" @click="submitUpload"></el-button>
            </el-col>
          </el-row>





        </el-tab-pane>
      </el-tabs>

    </section>
</template>

<script>
    export default {
        name: "StustudentPay",
      created() {

      },
      mounted() {
        this.initData();
      },
      methods: {
            initData(){
              this.allLoading=true;
              this.postRequest('/student/studentpay/initdata').then(resp => {
                this.studentInfo = resp.data[0];
                this.dormitoryInfo = resp.data[1];
                this.allLoading=false;
                console.info(JSON.stringify(resp.data))
              })
            },
        zhifubao(type,number){
          // this.postRequest('/alipay/page/gotoPayPage').then(resp => {
          //     console.info(resp.data);
          //     this.alipaydata=resp.data;
          //   console.info(""+this.alipaydata);
          // })

          // window.location.href="/alipay/page/gotoPayPage?subject=&totalAmount=15000&body="+this.studentInfo.sb.name+"15000";

          window.open("/alipay/page/gotoPayPage?subject="+type+"&totalAmount="+number+"&body="+this.studentInfo.sb.name+""+number+""+type,"_blank");

        },


      },
      data(){
        return {
          studentInfo:{},
          dormitoryInfo:{},
          allLoading:false,

          alipaydata: "",
        }
      }
    }
</script>

<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
  }
</style>



