<template>
  <section class="container">
    <!--       -->
    <div class="table"> <!-- begin -->
      <div class=""><!-- begin -->

        <el-row>
          <el-col :span="6">
            <div class="divbuju" style="height: 610px"> <!-- begin -->
              <!---->

                  <el-form :inline="true" :model="ddmcForm">
                    <el-form-item style="width: 100%">
                      
                      <el-select v-model="ddmcForm.majorId" placeholder="" @change="majorChange" clearable
                                 size="mini">
                        <el-option v-for="item in majorList" :key="item.id" :label="item.name"
                                   :value="item.majorId"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item style="width: 100%">
                      
                      <el-select v-model="ddmcForm.classId" placeholder=""  clearable size="mini">
                        <el-option v-for="item in classList" :key="item.id" :label="item.name"
                                   :value="item.classId"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item style="width: 100%">
                      
                      <el-input v-model="ddmcForm.dong" placeholder="" style="width:150px; heght:30px;"
                                size="mini"></el-input>
                    </el-form-item>
                    <el-form-item style="width: 100%">
                      
                      <el-input v-model.number="ddmcForm.floor" placeholder="" style="width:150px; heght:30px;"
                                size="mini"></el-input>
                    </el-form-item>

                    <el-form-item style="width: 100%">
                      
                      <el-select v-model="ddmcForm.sex" placeholder="" clearable size="mini">
                        <el-option v-for="item in sexOptions" :key="item.value" :label="item.label"
                                   :value="item.value"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item style="width: 100%">
                      
                      <el-select v-model="ddmcForm.number" placeholder="" clearable size="mini">
                        <el-option v-for="item in numberOptions" :key="item.value" :label="item.label"
                                   :value="item.value"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item style="width: 100%">
                      
                      <el-select v-model="ddmcForm.enough" placeholder="" clearable size="mini">
                        <el-option v-for="item in enoughOptions" :key="item.value" :label="item.label"
                                   :value="item.value"></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item style="width: 100%">
                      <el-button type="primary" icon="el-icon-search" @click="getalldormitory()" style="width: 250px" size="mini"></el-button>
                    </el-form-item>
                  </el-form>
            </div> <!-- end -->
          </el-col>

          <el-col :span="18">

            <div class="divbuju">


            <!--      -->
                    <el-row>
                      <el-col :span="4">
                        <div class="grid-content bg-purple">
                          <el-button type="success" icon="el-icon-circle-plus-outline" @click="handleAdd" size="mini" round>
                          </el-button>

                        </div>
                      </el-col>
                      <el-col :span="4">
                        <div class="grid-content bg-purple-light">
                          <el-button type="danger" icon="el-icon-delete" @click="handleDeleteList" size="mini" round></el-button>
                        </div>
                      </el-col>
                    </el-row>

            <template>
              <!---->
              <el-table :data="dormitoryList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        size="mini"
                        highlight-current-row border
                        class="el-tb-edit mgt20"
                        ref="multipleTable"
                        tooltip-effect="dark"
                        v-loading="dormitoryLoading"
                        @selection-change="selectChange">
                <!---->
                <el-table-column type="selection" width="55">
                </el-table-column>
                <!---->
                <el-table-column type="index" :index="indexMethod">
                </el-table-column>

                <el-table-column label="" prop="dong">
                </el-table-column>
                <el-table-column label="" prop="floor">
                </el-table-column>
                <el-table-column label="" prop="room">
                </el-table-column>
                <el-table-column label="" prop="sex">
                </el-table-column>
                <el-table-column label="" prop="number" :formatter="formatnumber" sortable>
                </el-table-column>
                <el-table-column label="//" prop="rent">
                </el-table-column>
                <el-table-column fixed="right" label="" width="250">
                  <template slot-scope="scope">  <!-- -->
                    <el-button plain size="small" @click="handleEdit(scope.$index,scope.row)"></el-button>
                    <el-button plain size="small" @click="getstudent(scope.row)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </template>
            <br>
            <br>
            <!--  -->
            <el-pagination @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="currentPage"
                           :page-sizes="[5, 10, 20, 40]"
                           :page-size="pagesize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="dormitoryList.length">
            </el-pagination>
            </div>
          </el-col>

        </el-row>

      </div><!-- end-->
    </div><!-- end -->

    <!--or-->
    <el-dialog title="or" :visible.sync="addandeditFromVisible" :close-on-click-modal="false">
      <el-form :model="addandeditFrom" label-width="80px"  ref="addandeditFrom">
        <el-form-item label="" prop="dong">
          <el-input v-model="addandeditFrom.dong" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="floor">
          <el-input v-model.number="addandeditFrom.floor" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="room">
          <el-input v-model.number="addandeditFrom.room" auto-complete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="sex">
          <el-select v-model="addandeditFrom.sex" placeholder="" clearable size="mini">
            <el-option v-for="item in sexOptions" :key="item.value" :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="" prop="number">
          <el-select v-model="addandeditFrom.number" placeholder="" clearable size="mini">
            <el-option v-for="item in numberOptions" :key="item.value" :label="item.label"
                       :value="item.value"></el-option>
          </el-select>
        </el-form-item>


        <el-form-item label="//" prop="rent">
          <el-input   v-model.number="addandeditFrom.rent" auto-complete="off"> </el-input >
        </el-form-item>



      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addandeditFromVisible=false"></el-button>
        <el-button type="primary" @click="addeditSubmit" ></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dormitoryShowInfoVisible" :close-on-click-modal="false" v-loading="dormitoryShowInfoLoading">
      <div>
        {{dormitoryClass}}
        <div>
          <template>
            <!---->
            <el-table :data="dormitoryStudent"
                      size="mini"
                      highlight-current-row border
                      class="el-tb-edit mgt20"
                      ref="multipleTable"
                      tooltip-effect="dark"
                      v-loading="dormitoryShowInfoLoading"
                     >
              <!---->
              <el-table-column type="index" :index="indexMethod">
              </el-table-column>
              <el-table-column label="" prop="c.m.name">
              </el-table-column>
              <el-table-column label="" prop="c.name">
              </el-table-column>
              <el-table-column label="" prop="sb.name">
              </el-table-column>
              <el-table-column label="" prop="studentId">
              </el-table-column>
              <el-table-column label="" prop="sb.sex">
              </el-table-column>
              <el-table-column label="" prop="sb.phone">
              </el-table-column>
              <el-table-column label="" prop="sb.email">
              </el-table-column>

            </el-table>
          </template>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dormitoryShowInfoVisible=false"></el-button>
      </div>
    </el-dialog>


  </section>
</template>

<script>
  export default {
    name: "DroDormitory",

    created() {
        this.getAllMajor();
    },
    mounted() {
      this.getalldormitory();

    },
    methods: {
        getalldormitory(){
          this.dormitoryLoading=true;
          this.postRequest('/dormitory/alldormitory/getallDormitory', {
            majorId: this.ddmcForm.majorId,
            classId: this.ddmcForm.classId,
            dong: this.ddmcForm.dong,
            floor: this.ddmcForm.floor,
            sex: this.ddmcForm.sex,
            number: this.ddmcForm.number,
            enough:this.ddmcForm.enough
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              console.info(JSON.stringify(data))
              this.dormitoryList = data;
              this.dormitoryLoading = false;
            }
          });
        },
      //
      majorChange(id) {
        this.classList = [],
          this.postRequest('/dormitory/alldormitory/getClassByMajorId', {
            majorId: this.ddmcForm.majorId
          }).then(resp => {
            if (resp && resp.status == 200) {
              var data = resp.data;
              this.classList = data;
              console.info("" + JSON.stringify(this.classList))
            }
          });
      },
      // currentPagepagesizedata
      handleSizeChange: function (size) {
        this.pagesize = size;
        console.log(this.pagesize)  //
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        console.log(this.currentPage)  //
      },
      indexMethod(index) {
        return index + 1;
      },

      //
      selectChange: function (val) {
        this.selectList = val;
      },

      //
      handleDeleteList: function () {
        const length = this.selectList.length;
        let id = "";
        for (let i = 0; i < length; i++) {
          id += this.selectList[i].id + ",";
        }

        //,
        id = id.substring(0, id.length - 1);
        console.info(id);
        this.$confirm("?", "", {
          type: "warning"
        }).then(() => {
          this.postRequest('/dormitory/alldormitory/deleteDormitory', {
            id: id
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.selectList = [];
              this.getalldormitory();
            }
          });
        })
          .catch(() => {
          });
      },
      // 
      handleAdd(){
          this.addandeditFromVisible=true;
      },
      handleEdit(index,row){
        this.addandeditFromVisible=true;
        this.addandeditFromType="edit";
        this.addandeditFrom.id=row.id;
        this.addandeditFrom.dong=row.dong;
        this.addandeditFrom.floor=row.floor;
        this.addandeditFrom.room=row.room;
        this.addandeditFrom.sex=row.sex;
        this.addandeditFrom.number=row.number;
        this.addandeditFrom.rent=row.rent;

      },
      addeditSubmit(){
          if(this.addandeditFromType=="edit"){
            this.postRequest('/dormitory/alldormitory/updatetDormitory', {
              id: this.addandeditFrom.id,
              dong:this.addandeditFrom.dong,
              floor:this.addandeditFrom.floor,
              room:this.addandeditFrom.room,
              sex:this.addandeditFrom.sex,
              number:this.addandeditFrom.number,
              rent:this.addandeditFrom.rent
            }).then(resp => {
              if (resp && resp.status == 200) {
                this.getalldormitory();
              }
            });
          }else{
            this.postRequest('/dormitory/alldormitory/insertDormitory', {
              dong:this.addandeditFrom.dong,
              floor:this.addandeditFrom.floor,
              room:this.addandeditFrom.room,
              sex:this.addandeditFrom.sex,
              number:this.addandeditFrom.number,
              rent:this.addandeditFrom.rent
            }).then(resp => {
              if (resp && resp.status == 200) {
                this.getalldormitory();
              }
            });
          }
      },



      //
      getstudent(row){
        this.dormitoryShowInfoLoading=true;
        this. dormitoryShowInfoVisible=true;
        var _this=this;
        this.postRequest('/dormitory/alldormitory/getStudentByDormitoryId', {
          dormitoryId:row.id
        }).then(resp => {
          if (resp && resp.status == 200) {
            console.info(JSON.stringify(resp.data))
            _this.dormitoryShowInfoLoading=false;
            let cs=resp.data[0];
            let classname="";
            if(cs.length>0){
              for(let i=0;i<cs.length;i++){
                classname+=cs[i].m.name+cs[i].name+""
              }
            }
            classname = classname.substring(0, classname.length - 1);
            this.dormitoryClass=classname;
            this.dormitoryStudent=resp.data[1];
            console.info(JSON.stringify(resp.data[0])+JSON.stringify(resp.data[1]));

          }
        });
      },

      //
      getAllMajor(){
        this.postRequest('/dormitory/alldormitory/getAllMajor', {
        }).then(resp => {
          if (resp && resp.status == 200) {
              this.majorList=resp.data;
          }
        });

      },



    },
    data() {
      return {
        ddmcForm: {
          majorId: "",
          classId: "",
          dong: "",
          floor: 0,
          sex: "",
          number: 0,
          enough:""
        },
        dormitoryList:[],
        dormitoryLoading:false,

        //
        classList: [],

        //
        majorList: [],

        //
        sexOptions: [
          {
            value: "",
            label: ""
          },
          {
            value: "",
            label: ""
          }
        ],
        //
        numberOptions: [
          {
            label: "2",
            value: 2
          },
          {
            label: "3",
            value: 3
          },
          {
            label: "4",
            value: 4
          },
          {
            label: "5",
            value: 5
          }
        ],
        //
        enoughOptions: [
          {
            value: "1",
            label: ""
          },
          {
            value: "0",
            label: ""
          },
        ],
        selectList:[],
        currentPage: 1, //
        pagesize: 10,    //    

        formatnumber: function (row, column) {
          return row.number == 2 ? "2"
            : row.number == 3 ? "3"
              :row.number == 4 ? "4"
                : row.number == 5 ? "5"
                  : "";
        },

        addandeditFromVisible:false,
        addandeditFrom:{
          id:"",
          dong:"",
          floor:0,
          room:0,
          sex: "",
          number: 0,
          rent:0.0
        },
        addandeditFromType:"",


        //
        dormitoryStudent:[],
        dormitoryClass:"",
        dormitoryShowInfoLoading:false,
        dormitoryShowInfoVisible:false,

      }

    }
  }
</script>

<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }
</style>



