<template>
  <section>
    <!--       -->
    <div class="table"> <!-- begin -->
      <div class="container"><!-- begin -->
        <div class="handle-box"> <!-- begin -->
          <!---->
          <el-row :gutter="20">
            <el-col :span="12" class="toolbar" style="padding-bottom: 0px;">
              <el-form :inline="true" :model="filters">
                <el-form-item>
                  
                  <el-input v-model="filters.username" placeholder="" style="width:150px; heght:30px;"
                            size="mini"></el-input>
                </el-form-item>
                <el-form-item>
                  
                  <el-select v-model="filters.type" placeholder="" clearable size="mini" style="width:150px; heght:30px;">
                    <el-option v-for="item in filters.typeOptions" :key="item.value" :label="item.label"
                               :value="item.value"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" icon="el-icon-search" @click="handleUserList()" size="mini"></el-button>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="12">
              <div class="grid-content bg-purple-light"></div>
            </el-col>
          </el-row>
        </div> <!-- end -->
        <!--      -->
        <el-row>
          <el-col :span="4">
            <div class="grid-content bg-purple">
              <el-button type="success" icon="el-icon-circle-plus-outline" @click="handleAdd" size="mini" round>
              </el-button>
              <el-button type="danger" icon="el-icon-delete" @click="handleDeleteList" size="mini" round></el-button>
            </div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content bg-purple-light">
            </div>
          </el-col>
        </el-row>

        <template>
          <!---->
          <el-table :data="userList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                    size="mini"
                    highlight-current-row border
                    class="el-tb-edit mgt20"
                    ref="multipleTable"
                    tooltip-effect="dark"
                    v-loading="listLoading"
                    @selection-change="selectChange">
            <!---->
            <el-table-column type="selection" width="55">
            </el-table-column>
            <!---->
            <el-table-column type="index" :index="indexMethod">
            </el-table-column>

            <el-table-column label="" prop="username">
              <template slot-scope="scope">
                <el-button @click="handleSelect(scope.$index,scope.row)" type="text" size="small">
                  {{scope.row.username}}
                </el-button>
              </template>
            </el-table-column>
            <el-table-column label="" prop="password">
            </el-table-column>

            <el-table-column label="" prop="type" :formatter="formatType" sortable>
            </el-table-column>

            <el-table-column fixed="right" label="" width="150">
              <template slot-scope="scope">  <!-- -->
                <el-button type="primary" plain size="small" @click="handleRole(scope.$index,scope.row)"></el-button>
                <el-button size="small" @click="handleEdit(scope.$index,scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <br>
        <br>
        <!--  -->
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="[5, 10, 20, 40]"
                       :page-size="pagesize"
                       layout="total, sizes, prev, pager, next, jumper"
                       :total="userList.length">
        </el-pagination>
      </div><!-- end-->
    </div><!-- end -->
    <!---->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form :inline="true" :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="username">
                <el-input v-model.number="addForm.username" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="password">
                <el-input v-model="addForm.password" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="type">
                <el-select v-model="addForm.type" placeholder="" clearable>
                  <el-option v-for="item in typeOptions" :key="item.value" :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false"></el-button>
        <el-button type="primary" @click="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" :visible.sync="selectFormVisible" :close-on-click-modal="false">
      <el-form :inline="true" :model="selectForm" label-width="80px" ref="selectForm">
        <div v-if="selectForm.type == 'student_basic'">
          <el-form-item label="" prop="userName">
            <el-input v-model="selectForm.student.name" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="loginName">
            <el-input v-model="selectForm.student.idNumber" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="birthday">
            <el-date-picker v-model="selectForm.student.birthday" type="date" placeholder=""
                            value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="mobile">
            <el-input v-model="selectForm.student.phone" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="mobile">
            <el-input v-model="selectForm.student.parentPhone" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="email">
            <el-input v-model="selectForm.student.email" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="sex">
            <el-input v-model="selectForm.student.sex" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="homeAddress">
            <el-input v-model="selectForm.student.adress" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="homeAddress">
            <el-input v-model="selectForm.student.major" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
        </div>
        <div v-else>
          <el-form-item label="" prop="userName">
            <el-input v-model="selectForm.teacher.name" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="loginName">
            <el-input v-model="selectForm.teacher.teacherId" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="selectForm.teacher.password" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="mobile">
            <el-input v-model="selectForm.teacher.phone" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="selectForm.teacher.email" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;&nbsp; &nbsp;
          <el-form-item label="" prop="sex">
            <el-input v-model="selectForm.teacher.sex" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="homeAddress">
            <el-input v-model="selectForm.teacher.adress" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="selectFormVisible = false"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
      <el-form :inline="true" :model="editForm" label-width="80px" :rules="addFormRules" ref="editForm">
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="username">
                <el-input v-model.number="editForm.username" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="password">
                <el-input v-model="editForm.password" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="type">
                <el-select v-model="editForm.type" placeholder="" clearable>
                  <el-option v-for="item in typeOptions" :key="item.value" :label="item.label"
                             :value="item.value"></el-option>
                </el-select>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"></el-button>
        <el-button type="primary" @click="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="roleFormVisible" :close-on-click-modal="false">
      <el-form :model="selectForm" label-width="80px" ref="selectForm" :inline="true">
        <el-form-item label="" prop="positionName">
          <el-input v-model="selectForm.username" auto-complete="off" :disabled="true"></el-input>
        </el-form-item>
      </el-form>
      <el-form label-width="80px" :inline="true">
        <div v-for="item in selectForm.roles" :key="item.id">
          <el-form-item label="" prop="">
            <el-input v-model="item.name" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="">
            <el-input v-model="item.roleId" auto-complete="off" :disabled="true"></el-input>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="roleFormVisible = false"></el-button>
      </div>
    </el-dialog>

  </section>
</template>
<script>
  export default {
    name: "SysUser",

    data() {
      return {
        //
        currentPage: 1, //
        pagesize: 10,    //    
        userList: [],

        filters: {
          username: "",
          type: "",
          //
          typeOptions: [
            {
              value: "student_basic",
              label: ""
            },
            {
              value: "teacher",
              label: ""
            }
          ],
        },
        //table
        tableData: [],
        //Loading
        listLoading: false,
        //data
        selectList: [],
        //
        count: 0,
        currentPage: 1,
        pageSize: 10,
        //
        roleData: [],
        //
        addFormVisible: false,
        //Loading
        addLoading: false,
        //
        addFormRules: {
          username: [{required: true, message: "()", trigger: "blur"}],
          password: [
            {required: true, message: "", trigger: "blur"}
          ],
          type: [{required: true, message: "", trigger: "blur"}]
        },
        //
        addForm: {
          username: "",
          password: "",
          type: ""
        },
        //
        selectForm: {
          username: "",
          password: "",
          type: "",
          roles: [],
          student: {
            id: '',
            name: '',
            sex: '',
            birthday: '',
            phone: '',
            idNumber: '',
            email: '',
            parentPhone: '',
            adress: '',
            major: '',
            state: ''
          },

          teacher: {
            id: '',
            teacherId: '',
            password: '',
            name: '',
            sex: '',
            phone: '',
            email: '',
            adress: ''
          }
        },
        //
        selectFormVisible: false,
        //
        typeOptions: [
          {
            value: "student_basic",
            label: ""
          },
          {
            value: "teacher",
            label: ""
          }
        ],
        //
        editFormVisible: false,
        //Loading
        editLoading: false,
        //
        editForm: {
          id: "",
          username: "",
          password: "",
          type: ""
        },


        //
        infoForm: [],
        //Loading
        roleListLoading: false,
        //
        roleData: [],
        //
        roleFormVisible: false,


        //



      };
    },
    mounted() {
      this.handleUserList()
    },
    methods: {
      // currentPagepagesizedata
      handleSizeChange: function (size) {
        this.pagesize = size;
        console.log(this.pagesize)  //
      },
      handleCurrentChange: function (currentPage) {
        this.currentPage = currentPage;
        console.log(this.currentPage)  //
      },
      handleUserList() {
        var _this = this;

        this.postRequest('/system/sysuser/getallusers', {
          username: _this.filters.username,
          type: _this.filters.type
        }).then(resp => {
          if (resp && resp.status == 200) {
            var data = resp.data;
            console.info(JSON.stringify(data.obj));
            _this.userList = data;
          }
        });
      },
      //
      selectChange: function (val) {
        this.selectList = val;
      },
      formatType: function (row, column) {
        return row.type === "student_basic" ? "" : row.type === "teacher" ? "" : "";
      },
      indexMethod(index) {
        return index + 1;
      },
      //
      handleDeleteList: function () {
        let nowid=this.$store.state.user.id;
        const length = this.selectList.length;
        let id = "";
        for (let i = 0; i < length; i++) {
          if(nowid==this.selectList[i].id){
            this.$message({
              message: "",
              type: "error"
            });
            return ;
          }
          id += this.selectList[i].id + ",";
        }

        //,
        id = id.substring(0, id.length - 1);
        console.info(id);
        this.$confirm("?", "", {
          type: "warning"
        }).then(() => {
          this.listLoading = true;
          this.postRequest('/system/sysuser/deletelistusers', {
            id: id
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.listLoading = false;
              this.selectList = [];
              this.handleUserList();
            }
          });
        })
          .catch(() => {
          });
      },
      //
      handleAdd: function () {
        this.addFormVisible = true;
      },
      //
      addSubmit: function () {
        this.$refs.addForm.validate(valid => {
          if (valid) {
            //
            if (this.addForm.type == "") {
              this.addForm.sex = "teacher";
            }
            this.$confirm("", "", {}).then(() => {
              this.addLoading = true;
              this.postRequest('/system/sysuser/addusersinfo', {
                username: this.addForm.username,
                password: this.addForm.password,
                type: this.addForm.type
              }).then(resp => {
                if (resp && resp.status == 200) {
                  this.addLoading = false;
                  this.$refs["addForm"].resetFields();
                  this.addFormVisible = false;
                  this.handleUserList();
                }
              });
            });
          }
        });
      },

      // 
//row==={"id":1,"username":"2019714","password":"0000","type":"teacher","roles":[{"id":8,"roleId":"ROLE_ADMIN","name":""}]}
      handleSelect: function (index, row) {
        this.selectFormVisible = true;
        var _this = this;
        this.postRequest('/system/sysuser/selectusersinfo', {
          username: row.username,
          type: row.type
        }).then(resp => {
          if (resp && resp.status == 200) {
            _this.selectForm.username = row.username;
            _this.selectForm.password = row.password;
            _this.selectForm.type = row.type;
            _this.selectForm.roles = row.roles;

            if (resp.data.msg == '') {
              _this.selectForm.student = resp.data.obj;
            } else {
              _this.selectForm.teacher = resp.data.obj;
            }
            console.info(resp.data.msg == '');
          }
        });
        console.info(JSON.stringify(row));
        console.info(JSON.stringify(_this.selectForm));

      },

      //
      handleRole: function (index, row) {
        //
        this.roleFormVisible = true;
        this.roleListLoading = true;
        this.selectForm.username = row.username;
        this.selectForm.type = row.type;
        this.selectForm.roles = row.roles;
        this.roleListLoading = false;
      },


      //
      handleEdit: function (index, row) {
        this.editFormVisible = true;
        this.editForm.id = row.id;
        this.editForm.username = row.username;
        this.editForm.password = row.password;
        this.editForm.type = row.type;
      },
      //
      editSubmit: function () {
        let nowid=this.$store.state.user.id;
        if(nowid==this.editForm.id){
          this.$message({
            message: "",
            type: "error"
          });
          return ;
        }else{
          //
          if (this.editForm.type == "") {
            //
            this.editForm.type = "student";
          }
          this.$refs.editForm.validate(valid => {
            if (valid) {
              this.$confirm("", "", {}).then(() => {
                this.addLoading = true;
                this.postRequest('/system/sysuser/updateusersinfo', {
                  id: this.editForm.id,
                  username: this.editForm.username,
                  password: this.editForm.password,
                  type: this.editForm.type
                }).then(resp => {
                  if (resp && resp.status == 200) {
                    this.addLoading = false;
                    this.$refs["editForm"].resetFields();
                    this.editFormVisible = false;
                    this.handleUserList();
                  }
                });
              });
            }
          });
        }
      },

    },

  };
</script>


<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
  }
</style>







































