<template>
  <section :Loading="allLoading">
    <el-tabs type="border-card" style="margin-top: 20px">
      <el-tab-pane label="">

        <el-row style="border-radius: 4px">
          <el-col :span="6">
            <el-link :underline="false"></el-link>
          </el-col>
          <el-col :span="18">
            <el-link :underline="false"> </el-link>

          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-link :underline="false"> {{dormitoryInfo.rent}}</el-link>
          </el-col>
          <el-col :span="18" v-if="rent">
            <el-button type="primary" @click="zhifubao('',dormitoryInfo.rent)"></el-button>
          </el-col>
          <el-col :span="18" v-else>
            <el-link :underline="false"> {{dormitoryInfo.rent}}</el-link>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-link :underline="false"> {{studentInfo.c.m.price}}</el-link>
          </el-col>
          <el-col :span="18" v-if="price">
            <el-button type="primary" @click="zhifubao('',studentInfo.c.m.price)"></el-button>
          </el-col>
          <el-col :span="18" v-else>
            <el-link :underline="false"> {{studentInfo.c.m.price}}</el-link>
          </el-col>
        </el-row>


        <!--           <span> {{{alipaydata}}}</span>-->

        <div v-html="alipaydata"></div>


      </el-tab-pane>

      <el-tab-pane label="">
        <div class="divbuju">
        <el-link :underline="false"><h2></h2></el-link>

        <el-link :underline="false">
          <br/>
          <br/>
          <h3> </h3><br/>
          <hr/>
          
          
          
          
        </el-link>

        <el-row>
          <el-col :span="4" :offset="20">
            <el-button type="success" @click="submitfile(10)" :disabled="isok"></el-button>
          </el-col>
        </el-row>
        </div>

        <div class="divbuju">
        <el-row>
          <el-col :span="4" >
              
          </el-col>
        </el-row>

        <el-row v-if="apply!=null">
          <el-btton type="text" style="color: red" @click="showapplyfile"></el-btton>  <span style="color: #8aff71">{{apply.state}}</span>
        </el-row>
        <el-row v-else>
            <h3></h3>
        </el-row>
        </div>

      </el-tab-pane>
    </el-tabs>

<!--    -->
    <el-dialog title="" :visible.sync="submitfileVisible" :close-on-click-modal="false">

      <div>
        <em> {{filenumber}}</em>
        <el-upload
          action="http://127.0.0.1:8000/api/UploadFile/"
          list-type="picture-card"
          :auto-upload="false"
          accept=".jpg, .jpeg, .png, .bmp, .doc, .docx, .JPG, .JPEG, .PBG, .BMP, .DOC, .DOCX "
          :limit="filenumber"
          :on-change="OnChange"
          :on-remove="OnRemove"
          :on-preview="handlePictureCardPreview"
          :before-remove="beforeRemove"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </div>

      <el-button @click="noapplyfiles"></el-button>
      <el-button type="primary" @click="addapplyfiles"></el-button>
    </el-dialog>

<!--    -->
    <el-dialog title="" :visible.sync="trackapplyFromVisible" :close-on-click-modal="false">
      <div class="divbuju" v-for="af in applyfiles">
          <el-row>
            <el-col :span="12">{{af.filename}}</el-col>
            <el-col :span="6"><el-button type="primary" @click="downloadfile(af.fileurl)" round></el-button></el-col>
            <el-col :span="6"><el-button type="primary" @click="submitfile(1,af.id)" round></el-button></el-col>
          </el-row>
          <el-row>
            <el-col>
             <span style="color: red">{{af.remarks}}</span>
            </el-col>
          </el-row>
      </div>
      <el-button @click="submitfile(10,0)"></el-button>
      <el-button @click="trackapplyFromVisible=false"></el-button>
    </el-dialog>


  </section>
</template>

<script>
  export default {
    name: "StustudentPay",
    created() {

    },
    mounted() {
      this.initData();
    },
    methods: {
      initData() {
        this.allLoading = true;
        this.postRequest('/student/studentpay/initdata').then(resp => {
          this.studentInfo = resp.data[0];
          this.dormitoryInfo = resp.data[1];
          this.price = resp.data[2];
          this.rent = resp.data[3];
          this.apply=resp.data[4];
          if(this.apply==null){
            this.isok=false;
          }
          this.allLoading = false;

          console.info("" + JSON.stringify(resp.data))
        })
      },
      zhifubao(type, number) {
        // this.postRequest('/alipay/page/gotoPayPage').then(resp => {
        //     console.info(resp.data);
        //     this.alipaydata=resp.data;
        //   console.info(""+this.alipaydata);
        // })

        // window.location.href="/alipay/page/gotoPayPage?subject=&totalAmount=15000&body="+this.studentInfo.sb.name+"15000";

        window.open("/alipay/page/gotoPayPage?username=" + this.studentInfo.sb.idNumber + "&subject=" + type + "&totalAmount=" + number + "&body=" + this.studentInfo.sb.name + "" + number + "" + type, "_blank");

      },
      submitfile(number,id) {
        if(this.apply.state==""){
          this.$message({
            message:"",
            type:"success"
          });
        }else{
          this.filenumber=number;
          if(id==undefined){
            this.fileid="";
          }else{
            this.fileid=id;
          }
          this.submitfileVisible = true;
        }
      },

      //
      downloadfile(fileurl){
        window.location="/student/studentpay/download?fileurl="+fileurl;
      },



      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },
      OnChange(file,fileList){
        this.fileList=fileList

      },
      OnRemove(file,fileList){
        this.fileList=fileList
      },

      fun(){
        console.log('------------------------')
        console.log(this.fileList)
      },
      noapplyfiles(){
        this.submitfileVisible=false;
        this.fileList=[];
      },


      addapplyfiles() {
        console.info("id"+this.fileid);

        for(let i=0;i<this.fileList.length;i++){
          console.info(JSON.stringify(this.fileList[i].raw));
          this.param.append("files",this.fileList[i].raw)
        }
        this.param.append("id",this.fileid);
        console.info(JSON.stringify(this.param.getAll("files")));

        this.uploadFileRequest('/student/studentpay/upload',this.param).then(resp => {
          this.submitfileVisible=false;
          this.initData();
          this.fileList=[];
          this.fileid="";
          this.param=new FormData();
          this.trackapplyFromVisible=false;
        })
      },
      showapplyfile(){
        this.postRequest('/student/studentpay/getApplyfile',{
          studentId:this.studentInfo.studentId
        }).then(resp => {
          this.applyfiles=resp.data;
          console.info("" + JSON.stringify(resp.data))
        })
        this.trackapplyFromVisible=true;
      },
    },
    data() {
      return {
        studentInfo: {},
        dormitoryInfo: {},
        allLoading: false,

        alipaydata: "",
        // 
        price: true,
        rent: true,

        submitfileVisible: false,

        uploadForm: new FormData(),

        param: new FormData(),
        form:{},
        count:0,
        fileList:[],
        dialogVisible:false,
        dialogImageUrl:'',

        //
        filenumber :10,


        //
        apply:{},

        //
        isok:true,

        //
        trackapplyFromVisible:false,

        //id
        fileid:"",
        //
        applyfiles:[],
      }
    }
  }
</script>

<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  }
</style>



