<template>
  <section>
    <!--       -->
    <div class="table"> <!-- begin -->
      <div class="container"><!-- begin -->
        <div class="handle-box"> <!-- begin -->
          <!---->
          <el-row :gutter="20">
            <el-col :span="12" class="toolbar" style="padding-bottom: 0px;">
              <el-form :inline="true" :model="filters">
                <el-form-item>
                  
                  <el-input v-model="filters.name" placeholder="" style="width:150px; heght:30px;"
                            size="mini"></el-input>
                </el-form-item>
                <el-form-item>
                  
                  <el-input v-model="filters.roleId" placeholder="" style="width:150px; heght:30px;"
                            size="mini"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" icon="el-icon-search" @click="handleUserList()" size="mini"></el-button>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="12">
              <div class="grid-content bg-purple-light"></div>
            </el-col>
          </el-row>
        </div> <!-- end -->
        <!--      -->
        <el-row>
          <el-col :span="4">
            <div class="grid-content bg-purple">
              <el-button type="success" icon="el-icon-circle-plus-outline" @click="handleAdd" size="mini" round>
              </el-button>
              <el-button type="danger" icon="el-icon-delete" @click="handleDeleteList" size="mini" round></el-button>
            </div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content bg-purple-light">
            </div>
          </el-col>
        </el-row>

        <template>
          <!---->
          <el-table :data="userList.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                    size="mini"
                    highlight-current-row border
                    class="el-tb-edit mgt20"
                    ref="multipleTable"
                    tooltip-effect="dark"
                    v-loading="listLoading"
                    @selection-change="selectChange">
            <!---->
            <el-table-column type="selection" width="55">
            </el-table-column>
            <!---->
            <el-table-column type="index" :index="indexMethod">
            </el-table-column>

            <el-table-column label="" prop="name">
            </el-table-column>
            <el-table-column label="" prop="roleId">
            </el-table-column>

            <el-table-column fixed="right" label="" width="550">
              <template slot-scope="scope">  <!-- -->
                <el-button size="small" @click="handleEdit(scope.$index,scope.row)"></el-button>

                <el-button type="success" icon="el-icon-circle-plus-outline" size="small" @click="handleAddUser(scope.$index,scope.row,'add')"></el-button>

                <el-button type="danger" icon="el-icon-delete" size="small" @click="handleAddUser(scope.$index,scope.row,'delete')"></el-button>

                <el-button type="success" icon="el-icon-circle-plus-outline" size="small" @click="handleAddmenu(scope.$index,scope.row,'add')"></el-button>

                <el-button type="danger" icon="el-icon-delete" size="small" @click="handleAddmenu(scope.$index,scope.row,'delete')"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </template>
        <br>
        <br>
        <!--  -->
        <el-pagination @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="currentPage"
                       :page-sizes="[5, 10, 20, 40]"
                       :page-size="pagesize"
                       layout="total, sizes, prev, pager, next, jumper"
                       :total="userList.length">
        </el-pagination>
      </div><!-- end-->
    </div><!-- end -->
    <!---->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form :inline="true" :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="name">
                <el-input v-model.number="addForm.name" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="roleId">
                <el-input v-model="addForm.roleId" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false"></el-button>
        <el-button type="primary" @click="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
      <el-form :inline="true" :model="editForm" label-width="80px" :rules="addFormRules" ref="editForm">
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="name">
                <el-input v-model.number="editForm.name" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="16">
            <div>
              <el-form-item label="" prop="roleId">
                <el-input v-model="editForm.roleId" auto-complete="off"></el-input>
              </el-form-item>
            </div>
          </el-col>
          <el-col :span="8">
            <div></div>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"></el-button>
        <el-button type="primary" @click="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>



    <!---->
    <el-dialog title="" :visible.sync="adduserFormVisible" :close-on-click-modal="false">
      <el-row :gutter="20">
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="ufilters">
            <el-form-item>
              
              <el-input v-model="ufilters.username" placeholder="" style="width:200px; heght:30px;"
                        size="mini"></el-input>
            </el-form-item>
            <el-form-item>
              
              <el-select v-model="ufilters.type" placeholder="" clearable size="mini">
                <el-option v-for="item in ufilters.typeOptions" :key="item.value" :label="item.label"
                           :value="item.value"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" icon="el-icon-search" @click="selecthandleAddUser" size="mini"></el-button>
            </el-form-item>
          </el-form>
        </el-col>

      </el-row>

      <template>
        <!---->
        <el-table :data="noAddRoleUser.slice((acurrentPage-1)*apagesize,acurrentPage*apagesize)"
                  size="mini"
                  highlight-current-row border
                  class="el-tb-edit mgt20"
                  ref="multipleTable"
                  tooltip-effect="dark"
                  v-loading="listLoading"
                  @selection-change="selectChange">
          <!---->
          <el-table-column type="selection" width="55">
          </el-table-column>
          <!---->
          <el-table-column type="index" :index="indexMethod">
          </el-table-column>

          <el-table-column label="" prop="username">
          </el-table-column>
          <el-table-column label="" prop="password">
          </el-table-column>

          <el-table-column label="" prop="type" :formatter="formatType" sortable>
          </el-table-column>

        </el-table>
      </template>
      <br>
      <br>
      <!--  -->
      <el-pagination @size-change="ahandleSizeChange"
                     @current-change="ahandleCurrentChange"
                     :current-page="acurrentPage"
                     :page-sizes="[5, 10, 20, 40]"
                     :page-size="apagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="noAddRoleUser.length">
      </el-pagination>
      <div slot="footer" class="dialog-footer">
        <el-button @click="adduserFormVisible = false"></el-button>
        <el-button type="primary" @click="adduserSubmit" :loading="adduserLoading"></el-button>
      </div>
    </el-dialog>



<!--  -->

    <el-drawer
      title=""
      :visible.sync="drawer"
      :direction="rtl"
      :close-on-press-escape="true"
    >
      <div style=" width:45%;float:right;">
        <el-button type="success" icon="el-icon-search" @click="saveRoleacl" size="mini"></el-button>
        <el-tree :data="treeData" class="mgt20" show-checkbox default-expand-all node-key="id" ref="tree" highlight-current :check-strictly=true :props="defaultProps">
        </el-tree>
      </div>

    </el-drawer>



  </section>
</template>
<script>
export default {
  name: "SysRole",
  data() {
    return {
      //
      currentPage: 1, //
      pagesize: 10,    //    
      userList: [],

      filters: {
        name: "",
        roleId: "",
      },

      ufilters: {
        username: "",
        type: "",
        //
        typeOptions: [
          {
            value: "student_basic",
            label: ""
          },
          {
            value: "teacher",
            label: ""
          }
        ],
      },

      //Loading
      listLoading: false,
      //data
      selectList: [],

      //
      addFormVisible: false,
      //Loading
      addLoading: false,
      //
      addFormRules: {
        name: [{required: true, message: "", trigger: "blur"}],
        roleId: [
          {required: true, message: "", trigger: "blur"}
        ]
      },
      //
      addForm: {
        name: "",
        roleId: ""
      },

      //
      editFormVisible: false,
      //Loading
      editLoading: false,
      //
      editForm: {
        id: "",
        name: "",
        roleId: ""
      },

      //
      adduserFormVisible: false,
      //Loading
      adduserLoading: false,
      //
      adduserForm: {
          id: "",
          name: "",
          roleId: "",
        index:"",
        row:[],
        //
        type:""
      },
      // 
      noAddRoleUser:[],
      acurrentPage: 1, //
      apagesize: 10,    //    

    //
      drawer:false ,

      addmenuForm:{
        id: "",
        type:""
      },
      defaultProps: {
        children: 'children',
        label: 'name'
      },

      treeData:[],

    };
  },
  mounted() {
    this.handleUserList()
  },
  methods: {
    // currentPagepagesizedata
    handleSizeChange: function (size) {
      this.pagesize = size;
      console.log(this.pagesize)  //
    },
    handleCurrentChange: function (currentPage) {
      this.currentPage = currentPage;
      console.log(this.currentPage)  //
    },
    handleUserList() {
      var _this = this;
      this.postRequest('/system/sysrole/getallrole', {
        name: this.filters.name,
        roleId: this.filters.roleId
      }).then(resp => {
        if (resp && resp.status == 200) {
          var data = resp.data;
          console.info(JSON.stringify(data.obj));
          _this.userList = data;
        }
      });
    },
    //
    selectChange: function (val) {
      this.selectList = val;
    },
    formatType: function (row, column) {
      return row.type === "student_basic" ? "" : row.type === "teacher" ? "" : "";
    },
    indexMethod(index) {
      return index + 1;
    },
    //
    handleDeleteList: function () {
      const length = this.selectList.length;
      let nowid =this.$store.state.user.roles[0].id;
      console.info(nowid);
      let id = "";
      for (let i = 0; i < length; i++) {

        if(nowid==this.selectList[i].id){
          this.$message({
            message: "",
            type: "error"
          });
          return ;
        }
        id += this.selectList[i].id + ",";
      }
      //,
      id = id.substring(0, id.length - 1);
      console.info(id);
      this.$confirm("?", "", {
        type: "warning"
      }).then(() => {
        this.listLoading = true;
        this.postRequest('/system/sysrole/deletelistrole', {
          id: id
        }).then(resp => {
          if (resp && resp.status == 200) {
            this.listLoading = false;
            this.selectList = [];
            this.handleUserList();
          }
        });
      })
        .catch(() => {
        });
    },
    //
    handleAdd: function () {
      this.addFormVisible = true;
    },
    //
    addSubmit: function () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            this.postRequest('/system/sysrole/addroleinfo', {
              name: this.addForm.name,
              roleId: this.addForm.roleId,
            }).then(resp => {
              if (resp && resp.status == 200) {
                this.addLoading = false;
                this.$refs["addForm"].resetFields();
                this.addFormVisible = false;
                this.handleUserList();
              }
            });
          });
        }
      });
    },

    //
    handleEdit: function (index, row) {
      console.info(JSON.stringify(row));
      this.editFormVisible = true;
      this.editForm.id = row.id;
      this.editForm.name = row.name;
      this.editForm.roleId = row.roleId;
    },
    //
    editSubmit: function () {
      this.$refs.editForm.validate(valid => {
        let nowid =this.$store.state.user.roles[0].id;
        if(nowid==this.editForm.id){
          this.$message({
            message: "",
            type: "error"
          });
          return ;
        }else{
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            this.postRequest('/system/sysrole/updateroleinfo', {
              id: this.editForm.id,
              name: this.editForm.name,
              roleId: this.editForm.roleId
            }).then(resp => {
              if (resp && resp.status == 200) {
                this.addLoading = false;
                this.$refs["editForm"].resetFields();
                this.editFormVisible = false;
                this.handleUserList();
              }
            });
          });
        }
        }
      });
    },
    // 
    handleAddUser: function (index, row,type) {
      this.noAddRoleUser=[];
        this.adduserFormVisible=true;
        this.addLoading=true;
        this.adduserForm.id=row.id;
      this.adduserForm.name=row.name;
      this.adduserForm.roleId=row.roleId;
      this.adduserForm.index=index;
      this.adduserForm.row=row;
      this.adduserForm.type=type;
        var _this=this;
      // 
      this.postRequest('/system/sysrole/getnoaddroleuserinfo', {
        roleId: row.roleId,
        type:type,
        username: this.ufilters.username,
        utype:this.ufilters.type
      }).then(resp => {
        if (resp && resp.status == 200) {
          _this.noAddRoleUser=resp.data;
          console.info(JSON.stringify(resp.data));
        }
      });
    },
    // 
    selecthandleAddUser:function(){
      this.noAddRoleUser=[];
      this.addLoading=true;
      var _this=this;
      // 
      this.postRequest('/system/sysrole/getnoaddroleuserinfo', {
        roleId:_this.adduserForm.roleId,
        type:_this.adduserForm.type,
        username: this.ufilters.username,
        utype:this.ufilters.type
      }).then(resp => {
        if (resp && resp.status == 200) {
          _this.noAddRoleUser=resp.data;
          this.addLoading=false;
          console.info(JSON.stringify(resp.data));
        }
      });
    },



    // currentPagepagesizedata
    ahandleSizeChange: function (size) {
      this.apagesize = size;
      console.log(this.apagesize)  //
    },
    ahandleCurrentChange: function (currentPage) {
      this.acurrentPage = currentPage;
      console.log(this.acurrentPage)  //
    },

    adduserSubmit: function () {
      var _this=this;
      const length = this.selectList.length;
      let nowid =this.$store.state.user.id;

      let id = "";
      for (let i = 0; i < length; i++) {
        if(nowid==this.selectList[i].id){
          this.$message({
            message: "",
            type: "error"
          });
          return ;
        }
        id += this.selectList[i].id + ",";
      }

      if(id==""){
        this.$message(
          {
            message:"",
            type:"warning"
          }
        );
      }else{
        //,
        id = id.substring(0, id.length - 1);
        console.info(id);
        console.info(_this.adduserForm.id);

        var wordtype="";
        if(_this.adduserForm.type!='add'){
          var wordtype="";
        }
        this.$confirm(""+wordtype+""+_this.adduserForm.name+"?", "", {
          type: "warning"
        }).then(() => {

          if(_this.adduserForm.type=='add'){
            this.postRequest('/system/sysrole/insertuserrole', {
              id: id,
              role_id: _this.adduserForm.id
            }).then(resp => {
              if (resp && resp.status == 200) {
                _this.listLoading = false;
                _this.selectList = [];
                _this.handleAddUser(_this.adduserForm.index,_this.adduserForm.row,'add');
              }
            });
          }else{
            this.postRequest('/system/sysrole/deleteinsertuserrole', {
              id: id,
              role_id: _this.adduserForm.id
            }).then(resp => {
              if (resp && resp.status == 200) {
                _this.listLoading = false;
                _this.selectList = [];
                _this.handleAddUser(_this.adduserForm.index,_this.adduserForm.row ,'delete');
              }
            });
          }



        })
          .catch(() => {
          });
      }

    },


    saveRoleacl: function() {
      var _this = this;
      const ids = this.$refs.tree.getCheckedKeys();
      console.log(this.$refs.tree.getCheckedKeys());
      let id = "";
      if (ids.length > 0) {
        for (let i = 0; i < ids.length; i++) {
          id += ids[i] + ",";
        }
        //,
        id = id.substring(0, id.length - 1);
        console.info(id);

          this.postRequest('/system/sysrole/addordelrolemenu', {
            menuid: id,
            roleid:this.addmenuForm.id,
            type: this.addmenuForm.type
          }).then(resp => {
            if (resp && resp.status == 200) {
              this.drawer=false;
            }
          });

      }else{
        this.$message({
          message: "",
          type: "error"
        });
      }
    },

    handleAddmenu: function(index,row,type){
      console.info(JSON.stringify(row));
      this.addmenuForm.id=row.id;
      this.addmenuForm.type=type;
      console.info(type);
      console.info(row);
      // 
      this.postRequest('/system/sysrole/getnoaddrolemenuuserinfo', {
        roleId: row.roleId,
        type:type,
      }).then(resp => {
        if (resp && resp.status == 200) {
          this.treeData=resp.data;
          console.info(JSON.stringify(resp.data));
          // 
          this.drawer=true;
        }
      });

    },
  },

};
</script>
<style scoped>
  .container {
    padding: 30px;
    background: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
  }
</style>



